<template>
	<div>
		<div class="gg">
			<div class="zz">
				<p>
					<router-link to='/shouye' tag='span' class='zii'>首页    〉</router-link>
					<router-link to='/wode' tag='span' class='zii'>我的  〉</router-link>
					<router-link to='/gzd' tag='span' class='zii'>我关注的小区</router-link>
				</p>
			</div>
			<div class="hh">
