						</span>
					</li>
					
				</ul>
			</div>
			
			
		</div>
	</div>
</template>

<script>
	
	export default {
	  
	  data(){
	  	return{
	  		hh:[
	  			{src:require("../../build/my/img1.png"),'a':'辉煌国际','b':'昌平区-回龙观','c':'2007年建成','d':'370505元/平','e':'板塔结合',},
	  			{src:require("../../build/my/img2.png"),'a':'万寿园','b':'昌平区-回龙观','c':'2007年建成','d':'370505元/平','e':'板塔结合',},
