<style>
	.gg{
		width: 100%;
		position: absolute;
		top: 0;
		background-color: #fff;
	}
	
	.hh{
		width: 100%;
		
	}
	.hh>ul{
		list-style: none;
	}
	.hh>ul>li{
		width: 92%;
		padding: 2vw 4%;
		display: flex;
		border-bottom: 0.1vw solid #FF8A00;
	}
	.hh>ul>li>img{
		width: 35%;
		height: 25vw;
		margin-right: 4vw;
	}
	.hh>ul>li>span{
		width: 70%;
	}
	.hh>ul>li>span>h3{
		font-size: 4.5vw;
	}
	.hh>ul>li>span>p{
		font-size: 3.5vw;
		color: #8B8B8B;
		margin-top: 1.2vw;
		position: relative;
	}
	.hh>ul>li>span>p>.byy{
		color: #FF8A00;
		position: absolute;
		top: 0;
		right: 0;
		font-size: 4vw;
	}
</style>


