<template>
	<div>
		<div class="fang">
			<ul class="fx" >
				<li v-for="(xx,i) in fx" :class="{'bz':xx.flag}" @click="x(xx,i)" >
					<span>{{xx.rw}}</span>
					<input type="checkbox" v-model="xx.flag"/>
				</li>
			</ul>
			<input type="button" id="fxqd" value="确定" />
		</div>
	</div>
</template>

<script>
	export default {
	  data(){
	  	return{
	  		fx:[
	  			{rw:'不限',flag:false},
				{rw:'1室',flag:false},
				{rw:'2室',flag:false},
				{rw:'3室',flag:false},
				{rw:'4室',flag:false},
				{rw:'5室',flag:false},
	  		]
	  	}
	  },
	  methods:{
	  	x(xx,i){
				this.fx.forEach(ff=>{	
					if (ff.flag!==this.fx[i].flag) {
						ff.flag=false
					}
				})	
				xx.flag=!xx.flag
			}
	  }

	}
</script>

<style>
	.fang{
		width: 100%;
		background-color: #fff;
		
	}
	.fx{
		width: 100%;
	}
	.fx>li{
		width: 100%;
		font-size: 4.5vw;
		padding: 3vw 4%;
		box-sizing: border-box;
		position: relative;
	}
	.fx>li>input{
		position: absolute;
		top: 5vw;
		right: 8%;
	}
	#fxqd{
		width: 100%;
		padding: 3vw 0;
		font-size: 4.5vw;
		text-align: center;
		background-color: #FE8A00;
		color: #fff;
	}
</style>


