<template>
	<div>
		<div class="sx">
			<ul class="sxx" >
				<li v-for="(s,i) in sx" @click="zs(s,i)" :class="{'bz':s.flag}">
					 <p>{{s.nr}}<span class="xz"></span></p>
					<div class="cld"  @click.stop>
						<quyu  v-if='i==0' v-show="s.flag" ></quyu>
						<jiage v-if='i==1' v-show="s.flag"></jiage>
						<fangxing v-if='i==2' v-show="s.flag"></fangxing>
						<gengduo v-if='i==3' v-show="s.flag"></gengduo>
					</div>
				</li>
			</ul>
		</div>
		
		
		<div class="hh">
			<router-link to='/xiangqing' tag='ul'>
				<li  v-for="h in hh">
					<img :src="h.src"/>
					<span>
						<h3 v-text="h.a"></h3>
						<h4 v-text="h.b"></h4>
						<p><span v-text="h.c"></span><span class="byy" v-text="h.d"></span></p>
						<p v-text="h.e"></p>
					</span>
				</li>
					
			</router-link>
		</div>
		
	</div>
</template>

<script>
	import quyu from './quyu.vue'
	import jiage from './jiage.vue'
	import fangxing from './fangxing.vue'
	import gengduo from './gengduo.vue'
	export default {
	  
	  data(){
	  	return{
	  		hh:[
	  			{src:require("../../build/SecondHandList/img1.png"),'a':'西便门外大街 2室1厅 89平','b':'南北 高楼层/6层','c':'签约价格：13244元/平','d':'1105万','e':'签约日期：2015年11月10日',},
	  			{src:require("../../build/SecondHandList/img2.png"),'a':'金桥国际 1室1厅 63平','b':'南北 高楼层/6层','c':'签约价格：13244元/平','d':'1105万','e':'签约日期：2015年11月10日',},
	  			{src:require("../../build/SecondHandList/img3.png"),'a':'花涧溪二期 2室2厅 90平','b':'南北 高楼层/6层','c':'签约价格：13244元/平','d':'1105万','e':'签约日期：2015年11月10日',},
	  			{src:require("../../build/SecondHandList/img3.png"),'a':'花涧溪二期 2室2厅 90平','b':'南北 高楼层/6层','c':'签约价格：13244元/平','d':'1105万','e':'签约日期：2015年11月10日',},
	  			{src:require("../../build/SecondHandList/img3.png"),'a':'花涧溪二期 2室2厅 90平','b':'南北 高楼层/6层','c':'签约价格：13244元/平','d':'1105万','e':'签约日期：2015年11月10日',},
	  		],
	  		sx:[
	  			{'nr':'区域',flag:false},
	  			{'nr':'价格',flag:false},
	  			{'nr':'房型',flag:false},
	  			{'nr':'更多',flag:false},
	  		],
	  		

	  	}
	  },
	  methods:{
	  	zs(s,i){
	  		this.sx.forEach(ff=>{
//				console.log(ff.flag)			
//				console.log(this.aa[i].flag)			
				if (ff.flag!==this.sx[i].flag) {
					ff.flag=false
				}
			})
	  		
	  		
	  		s.flag=!s.flag	
	  		
	  	},
	  	
	  	
	  },
	  components:{
	  	quyu,
	  	jiage,
	  	fangxing,
	  	gengduo
	  }

	}
</script>

<style>
	.bz{
		background-color: #fff;
		color: #FE8A00;
	}
	.sx{
		width: 100%;
		background-color: #eee;
	}
	.sxx{
		width: 100%;
		display: flex;
		border-bottom: 0.1vw solid #BDBDBD;
		list-style: none;
		position: relative;
	}
	.sxx>li{
		width: 25%;
		font-size: 4.5vw;
		position: relative;
	}
	.sxx>li>p{
		background-color: #fff;
		padding: 3vw 14%;
		border-bottom: 0.2vw solid #BDBDBD;
	}
	.sxx>li>p>span{
		position: absolute;
		top: 5.5vw;
		left: 55%;
		width: 0;
		height: 0;
		border-top: 2vw solid #909291;
		border-left: 2vw solid transparent;
		border-right: 2vw solid transparent;
	}
	.sxx>li>.cld{
		width: 100vw;
		background-color: #eee;
		position: absolute;
		z-index: 99;
	}
	.sxx>li:nth-child(2)>.cld{
		left: -25vw;
		background-color: burlywood;
	}
	.sxx>li:nth-child(3)>.cld{
		left: -50vw;
		background-color: cadetblue;
	}
	.sxx>li:nth-child(4)>.cld{
		left: -75vw;
		background-color: coral;
	}
	
	.hh{
		width: 100%;
	}
	.hh>ul{
		list-style: none;
	}
	.hh>ul>li{
		width: 92%;
		padding: 2vw 4%;
		display: flex;
		border-bottom: 0.1vw solid #FF8A00;
	}
	.hh>ul>li>img{
		width: 35%;
		height: 25vw;
		margin-right: 4vw;
	}
	.hh>ul>li>span{
		width: 70%;
	}
	.hh>ul>li>span>h3,h4{
		font-size: 4.3vw;
	}
	.hh>ul>li>span>p{
		font-size: 3.5vw;
		color: #8B8B8B;
		margin-top: 1.2vw;
		position: relative;
	}
	.hh>ul>li>span>p>.byy{
		color: #FF8A00;
		position: absolute;
		top: 0;
		right: 0;
		font-size: 4vw;
	}
</style>


