<template>
	<div>
		<div class="gengduo">
		<h4>区域</h4>
			<ul class="f">
				<li v-for="(q,i) in qu" :class="{'ysy':q.flag}" @click="x(q,i)">{{q.nr}}</li>
			</ul>
		<h4>面积（平米）</h4>
			<ul class="f">
				<li v-for="(m,i) in mian" :class="{'ysy':m.flag}" @click="c(m,i)">{{m.nr}}</li>
			</ul>	
		<h4>标签</h4>
			<ul class="f">
				<li v-for="(b,i) in biao" :class="{'ysy':b.flag}" @click="v(b,i)">{{b.nr}}</li>
			</ul>
		<h4>楼龄</h4>
			<ul class="f">
				<li v-for="(l,i) in lou" :class="{'ysy':l.flag}" @click="b(l,i)">{{l.nr}}</li>
			</ul>
		<h4>楼层</h4>
			<ul class="f">
				<li v-for="(c,i) in ceng" :class="{'ysy':c.flag}" @click="n(c,i)">{{c.nr}}</li>
			</ul>
		<input type="button" id="qk" value="清空条件" />
		<input type="button" id="fxqd" value="确定" />
		
		
		</div>
			
	</div>
</template>

<script>
	export default {
	  data(){
	  	return{
	  		qu:[
	  			{'nr':'南',flag:false},
	  			{'nr':'南北',flag:false},
	  			{'nr':'东',flag:false},
	  			{'nr':'西',flag:false},
	  			{'nr':'北',flag:false},
	  		],
	  		mian:[
	  			{'nr':'50以下',flag:false},
	  			{'nr':'50-70下',flag:false},
	  			{'nr':'70-90',flag:false},
	  			{'nr':'90-110',flag:false},
	  			{'nr':'110-130',flag:false},
	  			{'nr':'130-150',flag:false},
	  			{'nr':'150-200',flag:false},
	  			{'nr':'200以上',flag:false},
	  		],
	  		biao:[
	  			{'nr':'学区房',flag:false},
	  			{'nr':'地铁房',flag:false},
	  			{'nr':'不限购',flag:false},
	  			{'nr':'满两年',flag:false},
	  			{'nr':'满五唯一',flag:false},
	  			{'nr':'降价',flag:false},
	  			{'nr':'新上',flag:false},
	  			{'nr':'独家',flag:false},
	  			{'nr':'有钥匙',flag:false},
	  		],
	  		lou:[
	  			{'nr':'5年以内',flag:false},
	  			{'nr':'10年以内',flag:false},
	  			{'nr':'10-20年',flag:false},
	  			{'nr':'20年以上',flag:false},
	  		],
	  		ceng:[
	  			{'nr':'低楼层',flag:false},
	  			{'nr':'中楼层',flag:false},
	  			{'nr':'高楼层',flag:false},
	  		],
	  		
	  	}
	  },
	  methods:{
	  	x(q,i){
	  		this.qu.forEach(ff=>{	
				if (ff.flag!==this.qu[i].flag) {
					ff.flag=false
				}
			})	
			q.flag=!q.flag
	  	},
	  	c(m,i){
	  		this.mian.forEach(ff=>{	
				if (ff.flag!==this.mian[i].flag) {
					ff.flag=false
				}
			})	
			m.flag=!m.flag
	  	},
	  	v(b,i){
	  		this.biao.forEach(ff=>{	
				if (ff.flag!==this.biao[i].flag) {
					ff.flag=false
				}
			})	
			b.flag=!b.flag
	  	},
	  	b(l,i){
	  		this.lou.forEach(ff=>{	
				if (ff.flag!==this.lou[i].flag) {
					ff.flag=false
				}
			})	
			l.flag=!l.flag
	  	},
	  	n(c,i){
	  		this.ceng.forEach(ff=>{	
				if (ff.flag!==this.ceng[i].flag) {
					ff.flag=false
				}
			})	
			c.flag=!c.flag
	  	},
	  }

	}
</script>

<style>
	.ysy{
		border: 0.3vw solid #FE8A00;
		color: #FE8A00;
	}
	.gengduo{
		width: 100%;
		background-color: #fff;
		box-sizing: border-box;
		
	}
	.gengduo>h4{
		padding: 3vw 4%;
	}
	.f{
		width: 100%;
		display: flex;
		flex-wrap: wrap;
		color: #BDBDBD;
	}
	.f>li{
		width: 21%;
		padding: 1vw 0;
		font-size: 4.2vw;
		margin-left: 3%;
		margin-top: 1vw;
		margin-bottom: 1vw;
		text-align: center;
		border: 0.3vw solid gainsboro;
	}
	#qk{
		margin-top: 3vw;
		width: 100%;
		padding: 3vw 0;
		font-size: 4.5vw;
		text-align: center;
		color: #BDBDBD;
		background-color: #fff;
	}
</style>


