<template>
	<div>
		<div class="jiage">
			<ul class="jg">
				<li v-for="(jj,i) in j" :class="{'bz':jj.flag}" @click="h(jj,i)" >{{jj.nr}}</li>
			</ul>
			<p>自定义价格 <span>最小</span>-<span>最大</span><span style="background-color: #FE8A00;color: #fff;">确定</span></p>
		</div>
	</div>
</template>

<script>
	export default {
		data(){
			return{
				j:[
					{'nr':'不限',flag:false},
					{'nr':'100万一下',flag:false},
					{'nr':'100万-150万',flag:false},
					{'nr':'150万-200万',flag:false},
					{'nr':'200万-250万',flag:false},
					{'nr':'250万-300万',flag:false},
				]
			}
		},
		methods:{
			h(jj,i){
				this.j.forEach(ff=>{	
					if (ff.flag!==this.j.flag) {
						ff.flag=false
					}
				})	
				jj.flag=!jj.flag
			}
		},
		
	}
</script>

<style>
	.bz{
		background-color: #fff;
		color: #FE8A00;
	}
	.jiage{
		width: 100%;
		background-color: #fff;
	}
	.jg{
		width: 100%;
	}
	.jg>li{
		width: 100%;
		font-size: 4.5vw;
		padding: 3vw 4%;
		box-sizing: border-box;
	}
	.jiage>p{
		width: 100%;
		font-size: 4.5vw;
		padding: 3vw 4%;
		box-sizing: border-box;
	}
	.jiage>p>span{
		background-color: #eee;
		font-size: 4.3vw;
		padding: 0.5vw;
		margin: 0 2vw;
	}
</style>


