<template>
	<div>
		<div class="w">
			<div class="we">
				<span to='' class='fh' @click="$router.back(-1)"><</span>
				<router-link to='/wode' class='wode'></router-link>
	    		<router-link to='/xiazai' class='xiazai'></router-link>
	    		<div class="dl">
	    			<img src="../../build/my/logo.png"/>
	    			<router-link to="/denglu" tag="span">登录</router-link>
				</div>
			</div>
			<div class="wr" v-for="e in ee">
				<router-link :to='e.to' tag="div"><span class="quan" :class="e.class"></span><span class="wenzi" v-text="e.nr"></span><span class="jt"></span></router-link>
				
			</div>
		</div>
	</div>
</template>

<script>
	export default {
	  data(){
	  	return{
	  		ee:[
	  			{'to':'/gzd' ,'class':'fy','nr':'我关注的房源'},
	  			{'to':'/wode' ,'class':'xq','nr':'我关注的小区'},
	  			{'to':'/wode' ,'class':'xf','nr':'我关注的新房'},
	  			{'to':'/wode' ,'class':'jl','nr':'我的看房记录'},
	  		]
	  		
	  	}
	  }
	}
</script>

<style>
	
	.w{
		width: 100%;
		position: absolute;
		top: -24vw;
		padding: 7vw 0 0 0;
	}
	.we{
		width: 100%;
		height: 55vw;
		background: url(../../build/my/bg1.png) no-repeat;
		background-size: cover;
		position: relative;
		
	}
	.fh{
		width: 40%;
		height: 13vw;
		font-size: 9vw;
		color: whitesmoke;
		padding: 0 0 0 2vw;
	}
	.dl{
		width: 24%;
		height: 30vw;
		position: absolute;
		left: 50%;
		top: 20vw;
		margin-left: -13%;
	}
	.dl>img{
		width: 100%;
		height: 20vw;
	}
	.dl>span{
		text-align: center;
		line-height: 8vw;
		display: block;
		color: #fff;
		width: 100%;
		height: 8vw;
		border: 0.1vw solid #fff;
		background-color: rgba(000,000,000,0);
		font-size: 4.5vw;
	}
	.wr{
		width: 100%;
		background-color: #fff;
		margin-top: 4vw;
		
	}
	.wr>div{
		width: 100%;
		height: 15vw;
		display: flex;
		border-bottom: 0.1vw solid #BDBDBD;
	}
	.wr>div>.quan{
		display: inline-block;
		width: 16%;
		height: 15vw;
	}
	.wr>div>.fy{
		background: url(../../build/my/icon1.png) no-repeat center;
		background-size: 55% 55%;
	}
	.wr>div>.xq{
		background: url(../../build/my/icon2.png) no-repeat center;
		background-size: 55% 55%;
	}
	.wr>div>.xf{
		background: url(../../build/my/icon3.png) no-repeat center;
		background-size: 55% 55%;
	}
	.wr>div>.jl{
		background: url(../../build/my/icon4.png) no-repeat center;
		background-size: 55% 55%;
	}
	.wr>div>.wenzi{
		display: inline-block;
		height: 15vw;
		width: 68%;
		color: #000;
		font-size: 5vw;
		line-height: 15vw;
	}
	.wr>div>.jt{
		display: inline-block;
		width: 16%;
		height: 15vw;
		background: url(../../build/my/right.png) no-repeat center;
		background-size: 21% 30%;
	}
</style>


