<template>
	<div>
		<div class="quyu">
			<ul class="qy">
				<li v-for="(dd,i) in d" :class="{'bz':dd.flag}" @click="pd(dd,i)" @click.stop>{{dd.nr}}
					<ul class="fj" v-if="i==0" v-show="dd.flag" @click.stop>
						<li v-for="(fj,index) in f" :class="{'bz':fj.flag}" @click="qu(fj,index)" >{{fj.w}}
							<ul class="zq" v-if="index==2" v-show="fj.flag" @click.stop>
								<li v-for="(zq,index) in g" :class="{'bz':zq.flag}" @click="z(zq,index)" >{{zq.w}}</li>
							</ul>
							<ul class="zq" v-if="index==0" v-show="fj.flag" @click.stop>
								<li v-for="(zq,index) in gg" :class="{'bz':zq.flag}" @click="q(zq,index)" >{{zq.w}}</li>
							</ul>
						</li>
					</ul>
					<ul class="fj" v-if="i==1" v-show="dd.flag" @click.stop>
						<li v-for="(fj,index) in ff" :class="{'bz':fj.flag}" @click="q(fj,index)" >{{fj.w}}</li>
					</ul>
					
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	export default {
	  data(){
	  	return{
	  		d:[
	  			{'nr':'区域',flag:false,},
	  			{'nr':'地铁',flag:false,},
	  		],
	  		f:[
	  			{'w':'附近',flag:false},
		  		{'w':'不限',flag:false},
		  		{'w':'朝阳',flag:false},
		  		{'w':'海淀',flag:false},
		  		{'w':'昌平',flag:false},
		  		{'w':'丰台',flag:false},
		  		{'w':'石景山',flag:false},
			],
	  		ff:[
	  			{'w':'1号线',flag:false},
			  	{'w':'2号线',flag:false},
	  		],
	  		g:[
	  			{'w':'不限',flag:false},
			  	{'w':'安贞',flag:false},
			  	{'w':'公园',flag:false},
			  	{'w':'百子湾',flag:false},
			  	{'w':'北大',flag:false},
			  	{'w':'CBD',flag:false},
			  	{'w':'wao',flag:false},
	  		],
	  		gg:[
	  			{'w':'a',flag:false},
			  	{'w':'b',flag:false},
	  		],
	  		
	  	}
	  },
	  methods:{
	  	pd(dd,i){
	  		this.d.forEach(ff=>{
//				console.log(ff.flag)			
//				console.log(this.aa[i].flag)			
				if (ff.flag!==this.d[i].flag) {
					ff.flag=false
				}
			})	
			dd.flag=!dd.flag
	  	},
	  	qu(fj,index){
	  		this.f.forEach(ff=>{	
				if (ff.flag!==this.f[index].flag) {
					ff.flag=false
				}
			})	
			fj.flag=!fj.flag
	  	},
	  	q(fj,index){
	  		this.ff.forEach(ff=>{	
				if (ff.flag!==this.ff.flag) {
					ff.flag=false
				}
			})	
			fj.flag=!fj.flag
	  	},
	  	z(zq,index){
	  		this.g.forEach(ff=>{	
				if (ff.flag!==this.g.flag) {
					ff.flag=false
				}
			})	
			zq.flag=!zq.flag
	  	},
	  	q(zq,index){
	  		this.gg.forEach(ff=>{	
				if (ff.flag!==this.gg.flag) {
					ff.flag=false
				}
			})	
			zq.flag=!zq.flag
	  	},
	  	
	  },
	  

	}
</script>

<style>
	.quyu{
		width: 100%;
		height: 85vw;
	}
	.bz{
		background-color: #fff;
		color: #FE8A00;
	}
	.qy{
		width: 25%;
		height: 100%;
	}
	.qy>li{
		box-sizing: border-box;
		padding: 3vw 14%;
		width: 100%;
		font-size: 4.5vw;
	}
	.fj{
		width: 25%;
		height: 85vw;
		position: absolute;
		top: 0;
		left: 25vw;
		background-color: white;
		color: black;
	}
	.fj>li{
		box-sizing: border-box;
		padding: 3vw 14%;
		width: 100%;
		font-size: 4.5vw;
		
	}
	.zq{
		width: 50vw;
		height: 85vw;
		position: absolute;
		top: 0;
		left: 25vw;
		background-color: white;
		color: black;
	}
	.zq>li{
		box-sizing: border-box;
		padding: 3vw 14%;
		width: 100%;
		font-size: 4.5vw;
		
	}
	
</style>


