import Vue from 'vue'
import Router from 'vue-router'
//import HelloWorld from '@/components/HelloWorld'
import shouye from '@/components/shouye'
import wode from '@/components/wode'
import ershoufang from '@/components/ershoufang'
import denglu from '@/components/denglu'
import gzd from '@/components/gzd'
import zhuce from '@/components/zhuce'
import wangji from '@/components/wangji'
import xiangqing from '@/components/xiangqing'



Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'shouye',
      redirect: '/shouye'
    },
    {
      path: '/shouye',
      name: 'shouye',
      component: shouye,
    },
    {
      path: '/ershoufang',
      name: 'ershoufang',
      component: ershoufang,
    },
    {
      path: '/wode',
      name: 'wode',
      component: wode,
    },
    {
      path: '/gzd',
      name: 'gzd',
      component: gzd,
    },
    {
      path: '/denglu',
      name: 'denglu',
      component: denglu,
    },
    {
      path: '/zhuce',
      name: 'zhuce',
      component: zhuce,
    },
    {
      path: '/wangji',
      name: 'wangji',
      component: wangji,
    },
    {
      path: '/xiangqing',
      name: 'xiangqing',
      component: xiangqing,
    },
  ]
})



