<template>
	<div>
		<div class="zhong">
			<div class="zz">
				<p>
					<router-link to='/shouye' tag='span' class='zii'>首页 〉</router-link>
					<router-link to='/wode' tag='span' class='zii'>我的 〉</router-link>
					<router-link to='/denglu' tag='span' class='zii'>登录 </router-link>
				</p>
			</div>
			<div class="dd">
				<span @click="pu" :class="[flag?'ys':'yss']">普通登录</span>
				<span @click='yzm' :class="[flagg?'ys':'yss']">验证码快捷登录</span>
			</div>
			<pt v-show='flag'></pt>
			<yzm v-show='flagg'></yzm>
			<p>
				<router-link to='/zhuce' tag='span'>立即注册</router-link>
				<router-link to='/wangji' tag='span'>忘记密码</router-link>
			</p>
			
		</div>
	</div>
</template>

<script>
	import pt from './pt.vue'
	import yzm from './yzm.vue'
	export default {
	  
	  data(){
	  	return{
	  		flag:true,
	  		flagg:false,

	  	}
	  },
	   methods:{
	   	pu(){
	   		this.flag=true;
	   		this.flagg=false;
	   		
	   	},
	   	yzm(){
	   		this.flag=false;
	   		this.flagg=true;
	   	},
	   },
	   components:{
	   	pt,
	   	yzm,
	   }

	}
	
</script>

<style>
	input{
		padding-left: 3vw;
	}
	.ys{
		border-bottom: 1vw solid #FE8A00;
	}
	.yss{
		border-bottom: 1vw solid #999999;
	}
	.zhong{
		width: 100%;
		position: absolute;
		top: 0;
		background-color: #fff;
	}
	.zz{
		width: 100%;
	}
	.zz>p{
		width: 100%;
		font-size: 5vw;
		padding: 4vw 0;
		border-bottom: 0.1vw solid #999999;
	}
	.zii{
		display: inline-block;
		margin: 0 0 0 3vw;
	}
	.dd{
		width: 100%;
		font-size: 6vw;
		display: flex;
	}
	.dd>span{
		text-align: center;
		width: 50%;
		padding: 3vw 0;
	}
	.zhong>p>span{
		display: inline-block;
		width: 20%;
		font-size: 5vw;
		padding: 2vw 4vw;
	}
	.zhong>p>span:nth-child(1){
		float: left;
		color: #FE8A00;
	}
	.zhong>p>span:nth-child(2){
		float: right;
	}
</style>


